.loginContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
  
    .loginForm {
      background: white;
      padding: 2rem;
      border-radius: 8px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  
      .formGroup {
        margin-bottom: 1rem;
  
        label {
          display: block;
          margin-bottom: 0.5rem;
        }
  
        input {
          padding: 0.5rem;
          border: 1px solid #ccc;
          border-radius: 4px;
        }
      }
  
      button {
        width: 100%;
        padding: 0.75rem;
        background-color: #007bff;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
  
        &:hover {
          background-color: #0056b3;
        }
  
        &:not(:last-child) {
          margin-bottom: 1rem;
        }
      }
    }
  }
  
  .error {
    color: rgb(194, 0, 0);
    width: 200px;
  }