.card {
    background: white;
    padding: 16px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  
    h3 {
      margin: 0 0 8px;
    }
  
    p {
      margin: 0;
      color: #333;
    }
  
    .buttonGroup {
      margin-top: 10px;
  
      button {
        margin: 0 5px;
        padding: 5px 10px;
        background-color: #007bff;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
  
        &:hover {
          background-color: #0056b3;
        }
      }
    }
  }
  